import React, { useContext } from 'react';
import { AnchorLink } from "gatsby-plugin-anchor-links";
import { ThemeContext } from 'providers/ThemeProvider';
import { Container, Button } from 'components/common';
import imgAbout from 'assets/illustrations/dev.svg'; // graphql in later
import { Wrapper, AboutWrapper, Details, Thumbnail } from './styles';

const About = () => {
  const { theme } = useContext(ThemeContext);

  return (
    <Wrapper id="about" >
      <AboutWrapper as={Container}>
        <Thumbnail>
          <img src={imgAbout} alt="Pllfy Gspr Csaba autsiskola rlam" max-width="100%" max-height="100%" width="100%" height="100%"/>
        </Thumbnail>
        <Details theme={theme}>
          <h1>Rlunk</h1>
          <p>
          Plfi Gspr Csaba vagyok, tbb mint 10 ve oktatok B kategriban, az vek sorn dolgoztam mr udvarhelyszki auts iskolnak is, jelenleg pedig magnvllalkozknt sajt autkkal oktatok.
          </p>
          <p> 
          A #GPO autsiskolban a B kategria mellett, COD96 s BE kategris kpzseken is rszt vehetsz.  Ha minket vlasztasz, a minsgi oktats mellett segtnk a gyintzs minden fzisban, irodnkban lehetsget biztostunk az elmleti vizsgra val gyakorlsra is.
          </p>
          <Button as={AnchorLink} to="#contact">
            Jelentkezz
          </Button>
        </Details>
      </AboutWrapper>
    </Wrapper>
  );
};

export default About